import { DropdownCustomToggler } from 'app/modules/dropdown/DropdownCustomToggler';
import { DropdownMenu3 } from 'app/modules/dropdown/DropDownMenu3';
import clsx from 'clsx';
import React, {useState} from 'react';
import { Dropdown, OverlayTrigger } from 'react-bootstrap';
import { useSelector, shallowEqual, useDispatch } from "react-redux";
import { useNavigate } from 'react-router-dom';
import {RootState} from "../../../../setup"
import {toAbsoluteUrl} from '../../../../_metronic/helpers'
import { CardMenu } from './CardMenu';
import { collectionType } from '../common/common'
import { FormattedMessage } from 'react-intl';

const toolbarButtonMarginClass = 'ms-1 ms-lg-3',
  toolbarUserAvatarHeightClass = 'symbol-30px symbol-md-40px'

export default function TokenCard(props: any) {

  const {authToken, userID}: any = useSelector<RootState>(
    ({auth}) => ({
      authToken: auth.accessToken,
      userID: auth.user?.userID
    }), shallowEqual
  )

  const navigate = useNavigate()
  const dispatch = useDispatch()

  const handleEditClick = (id: string) => {
    navigate('/edit-patents', {state: id})
  }
 
  return (
    <>
    <div id="p-card" className="w-270px mx-3 my-3 cursor-pointer">
        <div className="card card-custom gutter-b card-stretch">
            <span className="card-body d-flex flex-column bg-white justify-content-between product-card">
                <div className='d-flex justify-content-between'>
                    <span onClick={() => props.openDialog(`delete-patent-${props.id}`)} className="cursor-pointer mt-3">
                      {props.isMinted == '1' && 
                        <a 
                          href={props.blockchain == 'Ethereum' ? `https://ropsten.etherscan.io/tx//${props.txHash}` : `https://baobab.scope.klaytn.com/tx/${props.txHash}?tabId=nftTransfer`} 
                          className='bg-success rounded text-white px-2'
                        >
                          <FormattedMessage id="ADMIN_COLLECTION_LIST.MINTED" />
                        </a>
                      }
                    </span>
                    <Dropdown className="dropdown dropdown-inline" align={'end'}>
                        <Dropdown.Toggle
                            className="btn btn-clean btn-hover-light-primary btn-sm btn-icon cursor-pointer"
                            variant="transparent"
                            id="dropdown-toggle-top-user-profile"
                            as={DropdownCustomToggler}
                        >
                          <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" fill="currentColor" className="bi bi-three-dots-vertical" viewBox="0 0 16 16">
                            <path d="M9.5 13a1.5 1.5 0 1 1-3 0 1.5 1.5 0 0 1 3 0zm0-5a1.5 1.5 0 1 1-3 0 1.5 1.5 0 0 1 3 0zm0-5a1.5 1.5 0 1 1-3 0 1.5 1.5 0 0 1 3 0z"/>
                          </svg>   
                        </Dropdown.Toggle>
                        <Dropdown.Menu className="dropdown-menu dropdown-menu-sm dropdown-menu-right mt-1">
                            <DropdownMenu3 
                              id={props.id} 
                              isMinted={props.isMinted}
                              open={props.open}
                              openDialog={props.openDialog}
                              handleClickOpen={props.handleClickOpen}
                              handleClose={props.handleClose}
                            />
                        </Dropdown.Menu>
                    </Dropdown> 
                </div>
                <span>
                  <div className="text-center rounded mb-7">
                      <img src={props.imagePath} className="w-200px h-200px" />
                  </div>
                  <div>
                    <div className="d-flex justify-content-between">
                      <div>
                        <h4 className="font-size-12">
                            <div className="text-muted">{props.collectionType}</div>
                        </h4>
                        <h4 className="font-size-15">
                            <div className="text-dark">{props.collectionName}</div>
                        </h4>
                        <h4 className="font-size-12">
                            <div className="text-muted"><FormattedMessage id="ADMIN_COLLECTION_LIST.BY" /> {props.artistName}</div>
                        </h4>
                      </div>
                      <div className="d-flex flex-column font-size-12 text-dark">
                        <h4 className="font-size-12 ms-auto">
                            <div className="text-muted"><FormattedMessage id="ADMIN_COLLECTION_LIST.NO_OF_TIERS" /> {props.noOfTiers}</div>
                        </h4>
                        <h6>
                          <label className="font-size-17">{props.blockchain}</label>
                        </h6>
                        </div>
                    </div>
                  </div>
                </span>
            </span>
        </div>
    </div>
    </>
  )
}
