/* eslint-disable no-script-url,jsx-a11y/anchor-is-valid */
import { useNavigate } from "react-router-dom";
import { FormattedMessage } from "react-intl";

export function DropdownMenu3(props: any) {

    const navigate = useNavigate();

    const routeTo = (routeName: string) => {
        navigate(routeName)
    }

    return (<>
        {/*begin::Navigation*/}
        <ul className="navi navi-hover" style={{paddingLeft: 0}}>
            <a href={`preview-collection?id=${props.id}`} className="navi-item cursor-pointer mt-3" style={{ listStyleType: "none" }} >
                <span className="navi-link">
                    <span className="navi-text ms-6 text-dark"><FormattedMessage id="CREATE_EDIT_ARTIST.OPTIONS.VIEW" /></span>
                </span>
            </a>
            {props.isMinted == '0' && 
                <>
                    <div className='separator my-2'></div>
                    <li 
                        className="navi-item cursor-pointer mt-3 text-dark" 
                        style={{ listStyleType: "none" }} 
                        onClick={() => props.openDialog(`delete-patent-${props.id}`)}>
                        <span className="navi-link">
                            <span className="navi-text ms-6 text-dark"><FormattedMessage id="CREATE_EDIT_ARTIST.OPTIONS.DELETE" /></span>
                        </span>
                    </li>
                </>
            }
        </ul>
        {/*end::Navigation*/}
    </>);
}
