import React, {FC, useEffect, useState} from 'react';
import {useSelector, shallowEqual, useDispatch} from 'react-redux';
import {useNavigate} from 'react-router-dom';
import { RootState } from '../../../setup';
import TokenCard from './components/TokenCard';
import { getCollectionMeta, deleteCollection } from './crud/collectionCRUD';
import {actions} from '../explore/redux/ecomRedux';
import * as auth from '../auth/redux/AuthRedux';
import {AlertDanger, AlertSuccess} from '../alerts/Alerts'
import { collectionType, blockchainAbbr } from './common/common';
import { FormattedMessage } from 'react-intl';
import { Navigate, Route, Routes } from "react-router-dom";


const YourCollections: React.FC = () => {

  const dispatch = useDispatch();
  const navigate = useNavigate();

  const sucMsg = localStorage.getItem('collectionDeleted')
  const [successResMessage, setSuccessResMessage] = useState(sucMsg)

  const failMsg = localStorage.getItem('collectionNotDeleted')
  const [failureResMessage, setFailureResMessage] = useState(failMsg)

  const [checked, setChecked] = useState(true)

  const [open, setOpen] = React.useState(false)

  if(sucMsg) {
    setTimeout(() => {
      localStorage.removeItem("collectionDeleted");
      setChecked(false);
      setTimeout(() => {
        setSuccessResMessage("");
      }, 200);
    }, 5000);
  }

  if(failMsg) {
    setTimeout(() => {
      localStorage.removeItem("collectionNotDeleted");
      setChecked(false);
      setTimeout(() => {
        setFailureResMessage("");
      }, 200);
    }, 5000);
  }

  const { collectionMeta }: any = useSelector<RootState>(
      ({ecom}) => ({
          collectionMeta: ecom.collectionsMeta
      }), shallowEqual
  )

  const { isAdmin }: any = useSelector<RootState>(
    ({ auth }) => ({
        isAdmin: auth.user?.isAdmin
    }),
    shallowEqual
  );

  useEffect(() => {
    getCollectionMeta()
      .then((response) => {
          const collectionMeta = response.data.collectionsMeta
          dispatch(actions.setCollections(collectionMeta))
      }).catch((error) => {
          localStorage.setItem('sessionTimeOut', 'Session timed out')
          dispatch(auth.actions.logout())
      })
  }, [])

  const handleClick = () => {
    navigate('/create');
  }

  const handleDeletePatent = (collectionId: string) => {
    console.log(collectionId)
    deleteCollection(collectionId)
        .then(
            (res) => {
                if(res.data.success) {
                    localStorage.setItem('collectionDeleted', res.data.message)
                    closeDialog(`delete-patent-${collectionId}`)
                    navigate('/your-collections')
                    window.location.reload()
                } else {
                    if(res.data.message) {
                        localStorage.setItem('collectionNotDeleted', res.data.message)
                        navigate('/your-collections')
                    } else {
                        localStorage.setItem('collectionNotDeleted', 'Failed to delete the collection.')
                        navigate('/your-collections')
                    }
                }
            }
        )
        .catch((error) => {
            localStorage.setItem('sessionTimeOut', 'Session timed out')
            // dispatch(auth.actions.logout())
        })
  }

  const openDialog = (id: string) => {
    const element: HTMLElement = document.getElementById(id)!
    element.style.display='block'
  }

  const closeDialog = (id: string) => {
    const element: HTMLElement = document.getElementById(id)!
    element.style.display='none'
  }

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };


  return (
      <>
        {isAdmin == '1' &&
          <>
            {successResMessage !== undefined && successResMessage !== null && successResMessage !== "" ? (
              <AlertSuccess message={successResMessage} checked={checked} />
              ) : null }
    
            {failureResMessage !== undefined && failureResMessage !== null && failureResMessage !== "" ? (
              <AlertDanger message={failureResMessage} checked={checked} />
              ) : null }
    
            {collectionMeta &&
              <div className='card mb-5 mb-xl-10 mt-5' id='kt_profile_details_view'>
                <div className='card-header'>
                  <div className='card-title m-0'>
                    <h3 className='fw-bolder m-0'><FormattedMessage id="ADMIN_COLLECTION_LIST.MY_COLLECTION" /></h3>
                  </div>
                  <div className='card-title m-0'>
                    <button 
                      onClick={handleClick} 
                      className='py-2 px-4 rounded'
                      style={{ background: '#ffd600', color: '#000', borderColor: '#0000' }} 
                    >
                      <FormattedMessage id="ADMIN_COLLECTION_LIST.CREATE_COLLECTION" />
                    </button>
                  </div>
                </div>
        
                <div className='d-flex flex-wrap justify-content-center card-body'>
                    {collectionMeta.length > 0 && collectionMeta.map((collection: any) => (
                      <div key={collection.id}>
                        <TokenCard 
                            id={collection.id}
                            imagePath={`${process.env.REACT_APP_API_COLLECTION_IMAGE_URL}/${collection.collection_image}`}
                            // currency={currency}
                            artistName={collection.artistName}
                            collectionName={collection.collection_name}
                            collectionType={collectionType[collection.collection_type]}
                            noOfTiers={collection.tiersData.length}
                            blockchain={blockchainAbbr[collection.blockchain]}
                            isMinted={collection.is_minted}
                            txHash={collection.tx_hash}
                            open={open}
                            openDialog={openDialog}
                            handleClickOpen={handleClickOpen}
                            handleClose={handleClose}
                          />
                          <div id={`delete-patent-${collection.id}`} style={{ display: 'none' }} className="modal">
                            <span onClick={() => closeDialog(`delete-patent-${collection.id}`)} className="close" title="Close Modal">&times;</span>
                            <form className="modal-content">
                            <div className="px-7 py-7">
                              <h3><FormattedMessage id="COLLECTION_DELETE_BOX.DELETE_COLLECTION" /></h3>
                              <p className='font-size-15'><FormattedMessage id="COLLECTION_DELETE_BOX.DELETE_COLLECTION_CONFIRMATION" /></p>
    
                              <div className="d-flex">
                                <button onClick={() => closeDialog(`delete-patent-${collection.id}`)} type="button" className="btn btn-primary"><FormattedMessage id="COLLECTION_DELETE_BOX.CANCEL" /></button>
                                <button onClick={() => handleDeletePatent(collection.id)} type="button" className="btn btn-danger ms-3"><FormattedMessage id="COLLECTION_DELETE_BOX.DELETE" /></button>
                              </div>
                            </div>
                            </form>
                          </div>
                        </div>
                      ))}
                      {collectionMeta.length == 0 && 
                        <div className='mx-auto my-auto'>
                          <span className='fs-4'><FormattedMessage id="ADMIN_COLLECTION_LIST.NO_COLLECTION" /></span>
                        </div>
                      }
                </div>
              </div>
            }
          </>
        }

        {isAdmin == '0' && 
          <Routes>
              <Route index element={<Navigate to={'/error/404'} /> }/>
          </Routes>
        }
      </>
  )
}

export default YourCollections;